<template>
  <div id="note">
    <head-top title="笔记"></head-top>

    <div class="search-input">
      <van-search placeholder="请输入搜索关键词" v-model="value" @search="onSearch" />
    </div>

    <van-pull-refresh v-model="isLoading" @refresh="getDataList" class="pull_fresh">
      <div class="main">
        <div class="item" v-for="(item, k) in searchData" :key="k" @click="showDetail(item)">
          <div class="title">{{item['SNTtile']||'缺少标题'}}</div>
          <div class="content">
            <r-quill-editor :funType="funType" :content="item['SNContent']" :is-edit="false"  :is-class="false"></r-quill-editor>
          </div>
          <div class="time">{{item['CreateTime'] | formatDate(item['CreateTime']) }}</div>
        </div>
      </div>
    </van-pull-refresh>
  </div>
</template>

<script>
import headTop from "@/components/headTop";
import RQuillEditor from "@/components/RQuillEditor";
import { Search, Icon } from "vant";
import { ModulePicture } from "@/utils/enum";
export default {
  name: "",
  components: {
    [Search.name]: Search,
    [Icon.name]: Icon,
    headTop,
    RQuillEditor
  },
  props: {},
  data() {
    return {
      dataList: [],
      isLoading: false,
      form: {
        courseID: this.$store.getters.courseID,
        studentID: this.$store.getters.studentID,
        pno: 1,
        psize: 10
      },
      funType: ModulePicture.StudentNote,
      value: ""
    };
  },
  created() {},
  mounted() {
    this.$nextTick(() => {
      this.getDataList();
    });
  },
  methods: {
    getDataList() {
      this.isLoading = true;
      this.dataList = [];
      this.$mainHttp.note
        .getList(this.form)
        .then(res => {
          if (res.Result && res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              this.dataList.push(item);
            });
          }
          this.isLoading = false;
        })
        .catch(err => {
          this.isLoading = false;
        });
    },
    showDetail(item) {
      this.$router.push({
        path: "/history/note/detail",
        query: {
          title: item["SNTtile"],
          content: item["SNContent"]
        }
      });
    },
    onSearch() {}
  },
  computed: {
    searchData() {
      if (!this.value) {
        return this.dataList;
      } else {
        return this.dataList.filter(
          r =>
            r["SNTtile"].indexOf(this.value) > -1 ||
            r["SNContent"].indexOf(this.value) > -1
        );
      }
    }
  },
  watch: {}
};
</script>

<style lang='less' scoped>
#note {
  background: #ebf0ef;
  .head-back {
    display: none;
  }
  .icon-fanhui {
    position: absolute;
    left: 15px;
    top: 15px;
    color: #fff;
    font-size: 1.8rem;
  }
  .icon-add {
    float: right;
    margin-right: 4vw;
    line-height: 4.3rem;
    color: #fff;
    font-size: 1.8rem;
  }
  .search-input {
    .van-search__content {
      background: #ebf0ef;
      .van-cell__value--alone {
        font-size: 1.2rem;
      }
      .van-field__left-icon {
        margin-right: 5px;
        position: absolute;
        right: 10px;
      }
    }
  }
  .main {
    margin: 1vh auto;
    width: 100vw;
    height: calc(100vh - 4.5rem - 54px - 2vh);
    overflow-y: auto;
    .item {
      position: relative;
      width: 90vw;
      padding: 0 3vw;
      margin: 1vh auto;
      margin-bottom: 1vh;
      text-align: left;
      // border-bottom: 1px solid #fff;
      background-color: #fff;

      word-break: break-all;
      word-wrap: break-word;

      img {
        width: 160px;
        height: 113px;
      }
      .title {
        padding: 1rem 0;
        // margin-right: 3rem;
        font-size: 1.5rem;
        line-height: 1.2;
        color: #555;
        position: relative;
        &::after {
          content: " ";
          position: absolute;
          bottom: 0;
          left: 0;
          height: 1px;
          width: 100%;
          transform: scaleY(0.1);
          background-color: #000;
        }
      }
      .content {
        margin: 0 -2vw;
        max-height: 20vh;
        overflow-y: auto;
        pre {
          white-space: pre-wrap;
          white-space: -moz-pre-wrap;
          white-space: -pre-wrap;
          white-space: -o-pre-wrap;
        }
      }
      .time {
        padding-top: 0.3rem;
        padding-bottom: 1rem;
        font-size: 1.2rem;
        line-height: 1.5;
        color: rgba(102, 102, 102, 1);
      }
    }
  }

  .pull_fresh {
    /deep/ .van-pull-refresh__track {
      height: calc(100vh - 4.5rem - 54px - 2vh);
    }
  }
}
</style>


